<template>
  <div class="store-details">
    <div class="crumbs-box ">
      <span class="crumbs">店铺管理</span>
      <span class="crumbs line">/</span>
      <span class="crumbs">店铺详情</span>
    </div>
    <div class="content-page" v-if="dataInfo">
      <div class="tips-tit">店铺状态</div>
      <div class="steps-box">
        <el-steps :active="storeStatus" >
          <el-step title="申请开店" description="" space="20%"></el-step>
          <el-step title="店铺审核" description="" space="20%"></el-step>
          <el-step title="开通店铺账号" description="" space="20%"></el-step>
          <el-step title="关闭店铺" description="" space="20%"></el-step>
        </el-steps>
      </div>
      <div class="details-status" v-if="isStatusLine">
        <span>当前状态</span>
        <span class="status" v-if="dataInfo.sh_status!=1">
          <span v-if="dataInfo.sh_status==0">待审核</span>
          <span v-else-if="dataInfo.sh_status==-1">已拒绝</span>
          <span v-else-if="dataInfo.sh_status==-2">已删除</span>
        </span>
        <span class="status" v-else>
          <span v-if="dataInfo.store_status==1">运营中</span>
          <span v-else-if="dataInfo.sh_status==0">已下架</span>
          <span v-else-if="dataInfo.sh_status==-1">已删除</span>
        </span>
      </div>
      <!-- 时间线 -->
      <div :class="['time-line-box', isTimeLine?'open-timeline': '']">
        <ul class="time-line-ul">
          <li v-for="(item, index) in timeLineList" :key="index">
            <div class="top">
              <span class="time">{{item.ymd}}</span>
              <span class="title">{{item.title}}</span>
            </div>
            <div class="time-hms">
              <span class="week">{{item.week}}</span>
              <span class="hms">{{item.hms}}</span>
            </div>
          </li>
        </ul>
        <div class="timeline-open-btn hover" @click="timeLineOpenOrClose">
          <div class="font" v-if="!isTimeLine">展开</div>
          <div class="font" v-else>收起</div>
        </div>
        <img src="../../assets/mengban.png"  class="mengban-img" v-if="!isTimeLine">
      </div>
      <!--  -->
      <div class="tips-tit" style="margin-top: 30px;">店铺详情</div>
      <div class="details-box">
        <el-row class="row-col">
          <el-col :span="12">店铺名称：{{dataInfo.store_name}}</el-col>
          <el-col :span="12">店铺主营分类：<span v-for="(item, index) in dataInfo.storecatestore" :key="index" class="span">{{item.title}}</span></el-col>
        </el-row>
        <el-row class="row-col">
          <el-col :span="12">店铺描述：{{dataInfo.description}}</el-col>
          <el-col :span="12">平台提成：10%</el-col>
        </el-row>
        <el-row class="row-col">
          <el-col :span="12">店铺风格：<span v-for="(item, index) in dataInfo.stylestore" :key="index" class="span">{{item.title}}</span></el-col>

        </el-row>
        <el-row class="row-col">
          <el-col :span="12" class="flex-start">店铺LOGO：<img :src="host+dataInfo.cover" class="store-logo" v-if="dataInfo.cover"></el-col>
          <el-col :span="12" class="flex-start">店铺经营品牌：
            <img v-for="(item, index) in dataInfo.brand" :key="index" :src="host+item.logo" class="store-logo">
          </el-col>
        </el-row>
        <el-row class="row-col" style="margin-bottom: 50px;">
          <el-col :span="24" class="flex-start">店铺主题：
            <img v-for="(item, index) in dataInfo.store_img" :key="index" :src="host+item.img_url" class="store-zt">
          </el-col>
        </el-row>
        <el-row class="row-col" style="margin-bottom: 50px;">
          <el-col :span="24" class="flex-start">店铺营业执照电子版：
            <img v-for="(item, index) in dataInfo.business_img" :key="index" :src="host+item" class="store-yyzz" />
          </el-col>
        </el-row>
        <el-row class="row-col">
          <el-col :span="24" class="flex-start">店铺联系人：{{dataInfo.nickname}}</el-col>
        </el-row>
        <el-row class="row-col">
          <el-col :span="24" class="flex-start">联系人手机号：{{dataInfo.mobile}}</el-col>
        </el-row>
        <el-row class="row-col">
          <el-col :span="24" class="flex-start">门店座机：{{dataInfo.telephone}}</el-col>
        </el-row>
      </div>
      <div class="admin-btn-box">
        <div class="btn btn1 hover" @click="gotoBack">返回</div>
        <div class="btn btn2 hover" @click="gotoEdit" v-if="dataInfo.sh_status==0">编辑</div>
      </div>

    </div>
  </div>
</template>
<script>
  import { Storedetails } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'store-details',
    data() {
      return {
        token: sessionStorage.getItem('token'),
        id: '', // 店铺id
        isStatusLine: false,
        storeStatus: 1,
        isTimeLine: false,
        timeLineList: [],
        dataInfo: {}
      }
    },
    computed: {
      ...mapState(["host"])
    },
    components: {},
    mounted() {
      this.id = this.$route.query.storeId;
      //
      this.getDataInfo();
    },
    methods: {
      statusLineFn() {
        this.isStatusLine = true;
        // 调整步骤状态style
        setTimeout(function() {
          $(".el-step__head.is-finish").css({
            'color': '#fff',
            'border-color' : '#EE5567'
          });
          $(".el-step__title.is-finish").css({
            'color': '#333',
          });
          $(".el-step__head.is-finish .el-step__icon").css({
            'background': '#EE5567'
          });
          //
          $(".el-step__title.is-process, .el-step__title.is-wait, .el-step__head.is-process").css({
            'color': '#999',
            'border-color' : '#999',
            'font-weight': '400'
          });
          $(".el-step__main").css({
            'margin-left': '-10px'
          });
          $(".el-step").eq(2).find(".el-step__main").css({
            'margin-left': '-25px'
          })
          $(".el-step.is-horizontal .el-step__line").css({
            'top': '20px'
          })
          $(".el-step__icon").css({
            'width': '40px',
            'height': '40px'
          })
        }, 50);
      },
      getDataInfo() {
        Storedetails({
          token: this.token,
          store_id: this.id
        }).then(res => {
          // console.log(res);
          let dataInfo = res.data;
          this.dataInfo = dataInfo;
          if (dataInfo.storelog.length > 0) {
            for(let i in dataInfo.storelog) {
              dataInfo.storelog[i].ymd = this.createTimeYMD(dataInfo.storelog[i].create_time);
              dataInfo.storelog[i].hms = this.createTimeHMS(dataInfo.storelog[i].create_time);
              dataInfo.storelog[i].week = this.createTimeWeek(dataInfo.storelog[i].create_time);
              let text = '';
              switch (parseInt(dataInfo.storelog[i].status)) {
                case 1:
                  text = '提交开店申请';
                  break;
                case 2:
                  text = '店铺通过审核';
                  break;
                case 3:
                  text = '开通店铺账号';
                  break;
                case 4:
                  text = '店铺被封';
                  break;
                case 5:
                  text = '关闭店铺';
                  break;
                case 6:
                  text = '店铺未通过审核';
                  break;
              }
              dataInfo.storelog[i].title = text
            }
          }
          dataInfo.storelog.length > 3 ? this.isTimeLine = false : this.isTimeLine = true;
          this.timeLineList = dataInfo.storelog;
          if (dataInfo.sh_status==0) {
            // 待审核
            this.storeStatus = 2
          } else if (dataInfo.sh_status==1&&dataInfo.store_status==1) {
            // 审核通过且启用
            this.storeStatus = 3
          } else if (dataInfo.sh_status==1&&dataInfo.dataInfo.store_status!=1){
            // 已关闭
            this.storeStatus = 4
          }
          this.statusLineFn();
          //
          if (dataInfo.business_img!='') {
            dataInfo.business_img = (dataInfo.business_img).split(",");
          }



        })
      },
      gotoBack() {
        this.$router.back(-1)
      },
      gotoEdit() {
        let status = this.util.power('1', '3');
        if (status) {
          this.changePage('/storeAddEdit_vip', {id: this.id})
        } else {
          this.$message.error('暂无权限')
        }
      },
      timeLineOpenOrClose() {
        this.isTimeLine = !this.isTimeLine
      },
      addTime(m){return m<10?'0'+m:m },
      createTimeYMD(value){
        let time = new Date(value*1000);
        let year = time.getFullYear();
        let month = time.getMonth()+1;
        let date = time.getDate();
        return year+'-'+this.addTime(month)+'-'+this.addTime(date);
      },// 2017-05-08
      createTimeHMS(value){
        let time = new Date(value*1000);
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        return this.addTime(hours)+':'+this.addTime(minutes)+':'+this.addTime(seconds);
      },// 10:31:27
      createTimeWeek(value){
        let time = new Date(value*1000);
        let week = time.getDay();
        let str = "";
        switch (week) {
          case 0:
            str = "星期日";
            break;
          case 1:
            str = "星期一";
            break;
          case 2:
            str = "星期二";
            break;
          case 3:
            str = "星期三";
            break;
          case 4:
            str = "星期四";
            break;
          case 5:
            str = "星期五";
            break;
          case 6:
            str = "星期六";
            break;
        }
        return str;
      },//
    }
  }
</script>
<style lang="less" scoped>
  .el-step__head.is-finish{
    color: red !important;
    border-color: red !important;
  }
</style>



